@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover-focus {
  &:hover,
  &:focus-visible {
    @content;
  }
}

@mixin center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin header-items-states {
  @include hover-focus {
    outline: none;
    color: #1c3374;

    svg {
      fill: #1c3374;
    }
  }

  &:active {
    color: #0266c1;

    svg {
      fill: #0266c1;
    }
  }

  &:disabled {
    opacity: 0.1;
  }
}

@mixin menu-items-states($color) {
  border: 1px solid transparent;

  &:hover {
    border-bottom: 1px solid $color;
  }

  &:focus-visible {
    outline: none;
    border: 1px solid $color;
  }

  &:active {
    border: 1px solid transparent;
  }

  &:disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 1px)) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: ($vp-1280 - 1px)) {
    @content;
  }
}

@mixin vp-1199 {
  @media (max-width: ($vp-1200 - 1px)) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: ($vp-1024 - 1px)) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: ($vp-768 - 1px)) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: ($vp-375 - 1px)) {
    @content;
  }
}

