html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.hidden {
  display: none;
}

.no-overflow {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #2d383f;
}

.header {
  background: #005588;
  position: absolute;
  width: 100%;
  padding: 40px 120px 0;
}
@media (max-width: 1199px) {
  .header {
    padding: 30px 45px 0;
  }
  .header.light {
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 15px 0;
  }
}

.header__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  gap: 30px;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .header__wrapper {
    flex-wrap: wrap;
    gap: 50px 30px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    display: grid;
    grid-template-columns: 26px 112px;
    gap: 8px 0;
    justify-content: space-between;
    align-items: start;
  }
}

.header__close {
  display: none;
  stroke: #2d383f;
}

.header__menu {
  fill: #ffffff;
}

.header.light .header__menu {
  display: none;
}

.header.light .header__close {
  display: block;
}

.header__logo {
  width: 205px;
  height: 62px;
  border: 1px solid transparent;
}
.header__logo svg {
  fill: #ffffff;
}
.header__logo:hover, .header__logo:focus-visible {
  outline: none;
  color: #1c3374;
}
.header__logo:hover svg, .header__logo:focus-visible svg {
  fill: #1c3374;
}
.header__logo:active {
  color: #0266c1;
}
.header__logo:active svg {
  fill: #0266c1;
}
.header__logo:disabled {
  opacity: 0.1;
}
@media (max-width: 1199px) {
  .header__logo {
    width: 144px;
    height: 44px;
    margin-left: 98px;
  }
  .header__logo svg {
    width: 144px;
    height: 44px;
  }
  .header__logo.dark svg {
    fill: #2d383f;
  }
  .header__logo.dark:hover, .header__logo.dark:focus-visible {
    outline: none;
    color: #1c3374;
  }
  .header__logo.dark:hover svg, .header__logo.dark:focus-visible svg {
    fill: #1c3374;
  }
  .header__logo.dark:active {
    color: #0266c1;
  }
  .header__logo.dark:active svg {
    fill: #0266c1;
  }
  .header__logo.dark:disabled {
    opacity: 0.1;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 112px;
    height: 34px;
    margin: 0;
  }
  .header__logo svg {
    width: 112px;
    height: 34px;
  }
}

.header__button {
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.header__button:hover, .header__button:focus-visible {
  outline: none;
  color: #1c3374;
}
.header__button:hover svg, .header__button:focus-visible svg {
  fill: #1c3374;
}
.header__button:active {
  color: #0266c1;
}
.header__button:active svg {
  fill: #0266c1;
}
.header__button:disabled {
